import vaultInterface from '@/hooks/vault-interface'
import { useCalculateChangeLpRate, useCalculateSlippageAmount } from '@/hooks/vaults/useVaultsHelper'
import useGlobalStore from '@/store/common/global'
import { VaultApiInfo } from '@/types'
import { TradeInputGroup } from '@cetus/design'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { useAccountStore } from '@cetus/stores'
import { Token } from '@cetus/types'
import { Box, Button, Text, VStack } from '@chakra-ui/react'
import { useEffect } from 'react'
import { VaultsAddResult } from '../detail/VaultsAddResult'

type VaultDepositModeProps = {
  isUnstableVault: boolean
  displayTokenA?: Token | undefined
  displayTokenB?: Token | undefined
  vaultId: string
  isReverse: boolean
  assetAction: string
  isCheckedZAP: boolean
  currentActionTab: string
  autoRefresh: () => void
  apiVaultInfo: VaultApiInfo
}
export default function VaultDepositMode(props: VaultDepositModeProps) {
  const {
    isUnstableVault,
    displayTokenA,
    displayTokenB,
    vaultId,
    isReverse,
    assetAction,
    isCheckedZAP,
    currentActionTab,
    autoRefresh,
    apiVaultInfo
  } = props
  const useVaultsAdd = vaultInterface.useVaultsAdd(isUnstableVault)
  const useAddButtonStatus = vaultInterface.useAddButtonStatus(isUnstableVault)
  const useGetContractVaultInfo = vaultInterface.useGetContractVaultInfo(isUnstableVault)
  const { currentAccount, isOpenWalletModal, onWalletModal } = useAccountStore()
  const { contractVault, lpTokenInfo, fetchClmmPoolFromContract, fetchVaultInfoFromContract, gtAumLimit, hardCapUSD, vaultTvl } =
    useGetContractVaultInfo(
      apiVaultInfo?.vaultsId,
      apiVaultInfo?.clmmPoolAddress,
      apiVaultInfo?.tokenA,
      apiVaultInfo?.tokenB,
      apiVaultInfo?.isReverse
    )

  // ÂàùÂßãÂåñadd hookÊó∂Âå∫ÂàÜ ‰º†ÂÖ•tokenA,BÊåâÁÖßÂêàÁ∫¶ÊñπÂêë
  const {
    amountInputA,
    amountInputB,
    balanceInfoA,
    balanceInfoB,
    amountValueA,
    amountValueB,
    preCalculateLoading,
    fixAmountA,
    inputTotalValue,
    handleAmountInputChange,
    calculateResult,
    showOnlySideTips,
    doAddAction,
    reCalculateResult,
    overHardtop,
    zapNumError,
    calculateLpLoading,
    inputTotalValueError
  } = useVaultsAdd(
    assetAction,
    autoRefresh,
    vaultId,
    isReverse ? displayTokenB : displayTokenA,
    isReverse ? displayTokenA : displayTokenB,
    isReverse,
    isCheckedZAP,
    hardCapUSD,
    vaultTvl
  )
  console.log('üöÄüöÄüöÄ ~ DepositMode.tsx:72 ~ VaultDepositMode ~ inputTotalValueError:', inputTotalValueError)

  console.log('üöÄüöÄüöÄ ~ DepositMode.tsx:71 ~ VaultDepositMode ~ showOnlySideTips:', showOnlySideTips)

  const { btnText, btnDisabled } = useAddButtonStatus(
    isCheckedZAP,
    assetAction,
    true,
    amountInputA,
    amountInputB,
    isReverse ? !fixAmountA : fixAmountA,
    balanceInfoA?.balanceFormat,
    balanceInfoB?.balanceFormat,
    displayTokenA,
    displayTokenB,
    isUnstableVault,
    zapNumError,
    calculateResult,
    overHardtop,
    inputTotalValueError
  )

  const { vaultsSlippage } = useGlobalStore()

  useEffect(() => {
    if (apiVaultInfo) {
      fetchClmmPoolFromContract(apiVaultInfo.clmmPoolAddress, true)
    }
  }, [apiVaultInfo?.clmmPoolAddress])

  useEffect(() => {
    if (vaultId) {
      fetchVaultInfoFromContract(vaultId)
    }
  }, [vaultId])

  // ÊúÄÂ∞èÂèØÊé•Êî∂
  const { amountLimit } = useCalculateSlippageAmount(Number(vaultsSlippage), calculateResult?.ft_amount, false)
  // Âç†ÊØî ÈùûÁ®≥ lp_token_treasury Á®≥ÂÆötotal_supply ÂàÜÂà´Ë°®Á§∫Ê±†Â≠êÊÄªlp_tokenÊï∞Èáè
  const { lpRate } = useCalculateChangeLpRate(
    calculateResult?.ft_amount,
    isUnstableVault ? contractVault?.lp_token_treasury : contractVault?.total_supply
  )

  const { isApp } = useWindowWidth()

  return (
    <>
      <Box w="100%" position="relative">
        <TradeInputGroup
          onClick={() => {}}
          from={{
            wrapStyle: { h: '108px' },
            balance: balanceInfoA?.balanceFormat || '',
            value: amountInputA,
            amountValue: amountValueA,
            loading: (isReverse ? fixAmountA : !fixAmountA) && preCalculateLoading && !isCheckedZAP,
            hideSelf: assetAction !== 'both' && assetAction !== displayTokenA?.coin_type,
            onChange: value => {
              handleAmountInputChange(value, !isReverse, true, amountInputB)
            },
            selectable: false,
            placeholder: '0.0',
            token: displayTokenA,
            onFocusChange: (focus: boolean) => {}
          }}
          to={{
            wrapStyle: { h: '108px' },
            balance: balanceInfoB?.balanceFormat || '',
            value: amountInputB,
            amountValue: amountValueB,
            loading: (isReverse ? !fixAmountA : fixAmountA) && preCalculateLoading && !isCheckedZAP,
            onFocusChange: (focus: boolean) => {},
            onChange: value => {
              handleAmountInputChange(value, isReverse, true, amountInputA)
            },
            selectable: false,
            hideSelf: assetAction !== 'both' && assetAction !== displayTokenB?.coin_type,
            placeholder: '0.0',
            token: displayTokenB
          }}
          iconParams={{
            xlinkHref: '#icon-icon_add',
            svgFill: 'text_caption',
            iconCursor: 'default'
          }}
          iconHover={false}
        />
      </Box>

      {gtAumLimit ? (
        <VStack w="100%" alignItems="start">
          <Text
            color="primary_yellow"
            fontSize="12px"
            textAlign="left"
            w="100%"
            bg="primary_yellow_opacity.10"
            p="12px"
            borderRadius="8px"
            lineHeight="20px"
          >
            The vault has reached its current capacity. New deposits are not accepted at this time.
          </Text>
        </VStack>
      ) : overHardtop ? (
        <VStack w="100%" alignItems="start" gap="20px">
          <Text
            color="primary_yellow"
            fontSize="12px"
            textAlign="left"
            w="100%"
            bg="primary_yellow_opacity.10"
            p="12px"
            borderRadius="8px"
            lineHeight="20px"
          >
            Your deposit has gone over the vault capacity. Try reducing your deposit amount.
          </Text>
        </VStack>
      ) : zapNumError ? (
        <VStack w="100%" pb={calculateResult || showOnlySideTips ? '0px' : '20px'} alignItems="start" gap="20px">
          <Text
            color="primary_yellow"
            fontSize="12px"
            textAlign="left"
            w="100%"
            bg="primary_yellow_opacity.10"
            p="12px"
            borderRadius="8px"
            lineHeight="20px"
          >
            Single-asset deposit can't be higher than $10,000 at a time.
          </Text>
        </VStack>
      ) : null}

      <Button
        width="100%"
        height="52px"
        borderRadius="12px"
        marginBottom="8px"
        fontWeight="500"
        fontSize={zapNumError ? (isApp ? '12px' : '15px') : '16px'}
        isLoading={preCalculateLoading}
        disabled={btnDisabled || preCalculateLoading || gtAumLimit || overHardtop}
        onClick={() => (!currentAccount?.address ? onWalletModal(true) : doAddAction())}
      >
        {/* {btnText || currentActionTab} */}
        {btnText || (isCheckedZAP ? 'Zap In' : 'Deposit')}
      </Button>

      {inputTotalValueError && (
        <VStack w="100%" alignItems="start">
          <Text
            color="primary_red"
            fontSize="12px"
            textAlign="left"
            w="100%"
            bg="primary_red_opacity.10"
            p="12px"
            borderRadius="8px"
            lineHeight="20px"
          >
            The input is too small. Zap mode is not available.
          </Text>
        </VStack>
      )}

      {/* Â±ïÁ§∫ËÆ°ÁÆóÁªìÊûú */}
      {(calculateResult || showOnlySideTips) && !inputTotalValueError && (
        <VStack w="100%" pb="20px" alignItems="start" gap="0">
          {assetAction !== 'both' && showOnlySideTips && (
            <Text color="text_paragraph" fontSize="12px" lineHeight="1.8" pb={calculateResult ? '20px' : '0'}>
              {showOnlySideTips}
            </Text>
          )}
          {calculateResult && (
            <VaultsAddResult
              amountLimit={amountLimit}
              inputTotalValue={inputTotalValue}
              preCalculateLoading={preCalculateLoading}
              calculateLpLoading={calculateLpLoading}
              lpRate={lpRate}
              lpDecimals={lpTokenInfo?.decimals}
              poolName={`${displayTokenA?.symbol} - ${displayTokenB?.symbol}`}
              labelColor="primary_gray"
            />
          )}
        </VStack>
      )}
      {/* {calculateResult && (
        <HStack marginBottom="12px" width="100%">
          <VaultsAddResult
            amountLimit={amountLimit}
            inputTotalValue={inputTotalValue}
            preCalculateLoading={preCalculateLoading}
            calculateLpLoading={calculateLpLoading}
            lpRate={lpRate}
            lpDecimals={lpTokenInfo?.decimals}
            poolName={`${displayTokenA?.symbol} - ${displayTokenB?.symbol}`}
            labelColor="primary_gray"
          />
        </HStack>
      )} */}
    </>
  )
}
